@import url('https://fonts.googleapis.com/css?family=Kalam:400,700|Source+Sans+Pro:300,300i,400,700&display=swap');

.light-mode {
  background-color: white;
}

.dark-mode{
  background-color: #333333;
}

img{
  width: 100%;
  height: auto;
}

body {
  margin: 0;
  background-color: white;
}

body,
td,
th,
tr,
p,
ul,
li,
a,
.content,
#content,
.header {
    font-family:'Source Sans Pro', 'Open Sans','Lato','Helvetica';
    font-size: 30px;
    font-weight:100;
    color: black;
  
}


.header {
  font-family: 'Source Sans Pro','Open Sans','Lato','Helvetica';
  font-size: 25px;
  overflow: hidden;
  color: black;
  text-align: center;
}

