a {
  text-decoration: none;
  background-image: linear-gradient(120deg, #d1aaf0  0%, #00b9d8 100%);
  background-repeat: no-repeat;
  background-size: 200% 0.2em;
  background-position: 0 90%;
  transition: background-size 0.25s ease-in;
}

/* a:focus, */
a:hover {
  background-size: 100% 88%;
}

ul.header li a:focus,
ul.header li a:hover{
  color: black;
  text-decoration: none;
}

h1{
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-family: 'Kalam', 'Source Sans Pro','Helvetica', 'Lato', 'Open Sans';
  font-size: 5em;
  text-align: center;
  color: black;
}

h2 {
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-size: 3em;
  color: black;
}

h3,
h3 span,
h3 a{
  font-family: 'Source Sans Pro', 'Courier','Open Sans','Lato','Helvetica';
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-size: 1em;
  font-weight: bolder;
  color: #333;
}


#language {
  font-weight: bold;
}

@media (max-width: 700px) and (min-width: 480px) {

  h2{
    font-size: 25px;
  }

  h3,
  h3 span,
  h3 a{
    font-size: 22px;
  }

  body,
  tr,
  p,
  a, 
  li {
      font-size: 20px;
  }
  
  strong {
      font-size: 20px;
  }


  ul.header li {
    display: inline;
    list-style-type: none;
  }
  
  ul.header {
    font-weight:100;
  }
  
  ul.header li a {
    color: black;
    /* display: inline-block; */
  }
  #header{
    width: 95%;
  }

}

@media (max-width: 480px) {
  h1{
    font-size: 22px;
  }

  h2{
    font-size: 20px;
  }

  body,
  h3,
  h3 span,
  h3 a,
  tr,
  p,
  a,
  li {
      font-size: 18px;
  }

  ul a, ul, #header ul{
    padding-inline-start: 0;
    font-size: 15px;
  }

  .main ul{
    padding-inline-start: 0%;
  }
  
  strong {
      font-size: 16px;
      /* font-weight: 400; */

  }

  #header{
    width: 95%;
    text-align: center;
  }
}